.chess-board {
  position: relative;
  border-top: 1px solid;
  border-bottom: 1px solid;
  border-color: #262626;
  overflow: hidden;
}
.chess-board .board-cols {
  font-weight: 500;
  font-size: large;
  position: absolute;
  display: grid;
  grid-template-columns: repeat(8, 90px);
  bottom: 0px;
  left: 75px;
}
.chess-board .board-rows {
  font-weight: 500;
  font-size: large;
  position: absolute;
  display: grid;
  grid-template-rows: repeat(8, 90px);
  top: 0px;
  left: 2px;
}
.chess-board .black-letter {
  color: rgb(119, 153, 84);
}
.chess-board .white-letter {
  color: rgb(233, 237, 204);
}
.chess-board .board-squares {
  display: grid;
  grid-template-columns: repeat(8, 90px);
  grid-template-rows: repeat(8, 90px);
}
.chess-board .board-squares .black-square {
  background-color: rgb(119, 153, 84);
  border-color: rgb(208, 219, 195);
}
.chess-board .board-squares .white-square {
  background-color: rgb(233, 237, 204);
  border-color: rgb(247, 249, 237);
}

.game-container {
  display: flex;
  flex-grow: 1;
  align-items: center;
  justify-content: center;
  height: 100%;
}
.game-container .game-content {
  display: flex;
  flex-direction: column;
  border: 1px solid;
  border-color: #262626;
  border-radius: 10px;
  height: 800px;
  width: 720px;
  overflow: hidden;
}
.game-container .game-content .game-sidebar {
  font-size: large;
  display: flex;
  align-items: center;
  width: calc(100% - 5px - 5px);
  height: 40px;
  padding: 0px 5px 0px 5px;
  background-color: #2f2f2f;
}
.game-container .game-content .game-sidebar .game-timer {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 55px;
  height: 30px;
  background-color: #5c5c5c;
  border-radius: 5px;
  margin-left: auto;
}
.game-container .game-content .game-sidebar .game-user-avatar {
  width: 30px;
  height: 30px;
  margin-right: 5px;
}
.game-container .game-content .game-sidebar .game-user-rating {
  color: grey;
  margin-left: 5px;
}

.play-page {
  display: flex;
  flex-grow: 1;
  justify-content: center;
  align-items: center;
}
.play-page .play-page-sidebar {
  display: flex;
  flex-direction: reverse;
  align-items: center;
  border-left: 1px solid;
  border-color: #363636;
  background-color: #2c2c2c;
  height: 100%;
  width: 350px;
}
.play-page .play-page-sidebar .sidebar-play-button {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 80%;
  height: 6vh;
  margin: auto;
  margin-top: 10px;
  background-color: rgb(65, 161, 65);
  border-radius: 15px;
  border-bottom: 4px solid;
  border-color: rgb(6, 141, 6);
}
.play-page .play-page-sidebar .sidebar-play-button:hover {
  background-color: rgb(62, 151, 62);
  border-color: rgb(6, 133, 6);
}
.play-page .play-page-sidebar .sidebar-play-button .sidebar-play-button-title {
  font-size: large;
  font-weight: 700;
}
.play-page .play-page-sidebar .sidebar-play-button .sidebar-play-button-content {
  font-size: small;
}

.app {
  display: flex;
}

body {
  background-color: #333333;
  color: white;
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace;
}

li {
  list-style-type: none;
}

ul {
  padding: 0px;
  margin: 0px;
}

a {
  text-decoration: none;
  color: white;
}

